.store__hero{

  img{
    object-fit: cover;
  }
&:before {
    display: block;
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    background: rgba(0,0,0,0.5);
    z-index: 1;
  }
}

.store__hero.main{
  height: 50vh;
}

.store__action {
  font-size: 10px;
  svg {
    width: 25px;
    fill: white
  }
}



