@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,700");
body {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 12px; }

.title a {
  background-color: #FFC40E; }

.title_main {
  top: 25vh; }

.inner {
  max-width: 1200px;
  width: 80vw;
  margin: 0 auto; }

.store__hero img {
  object-fit: cover; }

.store__hero:before {
  display: block;
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1; }

.store__hero.main {
  height: 50vh; }

.store__action {
  font-size: 10px; }
  .store__action svg {
    width: 25px;
    fill: white; }

input:not([type='file']), textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid grey; }
  input:not([type='file']).input--error, textarea.input--error {
    border-color: red; }

label {
  padding: 10px 0;
  display: block; }

.form {
  background: white;
  padding: 2rem; }
  .form + .form {
    margin-top: 4rem; }
  .form h2 {
    margin: 0;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px solid grey; }
  .form .button {
    border: 0;
    background: #FFC40E;
    color: black;
    font-weight: 600; }

.search {
  position: relative; }
  .search__results {
    background: white;
    position: absolute;
    width: 100%; }
  .search__input {
    background: none;
    color: white;
    outline: 0; }
  .search__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec; }

.map {
  padding: 20px;
  background: white; }

#map {
  height: 500px; }

.popup {
  width: 300px; }
  .popup img {
    width: 100%; }

.heart__button {
  background: none;
  border: 0;
  outline: 0;
  position: relative; }
  .heart__button:after {
    content: '♥️';
    font-size: 20px;
    position: absolute;
    opacity: 0;
    top: 0; }
  .heart__button svg {
    width: 25px;
    fill: white; }
  .heart__button--hearted svg {
    fill: red; }
  .heart__button--float:after {
    animation: fly 2.5s 1 ease-out; }

@keyframes fly {
  0% {
    transform: translateY(0);
    left: 0;
    opacity: 1; }
  20% {
    left: 20px; }
  40% {
    left: -20px; }
  60% {
    left: 20px; }
  80% {
    left: -20px; }
  100% {
    transform: translateY(-400px);
    opacity: 0;
    left: 20px; } }

.review {
  background: white;
  border: 1px solid grey;
  border-bottom: 0;
  border-bottom: 1px solid grey;
  margin-bottom: 2rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
  padding-left: 5px; }
  .review:before {
    width: 5px;
    left: 0;
    content: '';
    display: block;
    background: #303030;
    position: absolute;
    height: 100%;
    background-attachment: fixed; }
  .review__header {
    border-bottom: 1px solid grey;
    display: flex; }
    .review__header > * {
      border-right: 1px solid grey;
      padding: 0.2rem;
      display: flex;
      align-items: center;
      flex: 1;
      justify-content: center; }
      .review__header > *:last-child {
        border-right: 0; }
  .review__author {
    justify-content: flex-start;
    padding-left: 2rem;
    display: flex; }
    .review__author .avatar {
      margin-right: 2rem; }
  .review__time {
    font-size: 1.2rem;
    color: #999999; }
  .review__stars {
    color: #FFC40E; }
  .review__body {
    padding: 2rem; }
  .review p {
    white-space: pre-wrap; }

.reviewer {
  position: relative;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem; }
  .reviewer__stars {
    display: flex;
    justify-content: center; }
    .reviewer__stars input {
      display: none; }
      .reviewer__stars input:checked ~ label {
        color: #FFC40E; }
      .reviewer__stars input + label {
        font-size: 0;
        /* These are in the opposite DOM order
           re-order them to be visually in normal order
           This is fine for accessibility because our labels have for()
         */ }
        .reviewer__stars input + label:before {
          content: '★';
          font-size: 2rem; }
        .reviewer__stars input + label[for="star5"] {
          order: 5; }
        .reviewer__stars input + label[for="star4"] {
          order: 4; }
        .reviewer__stars input + label[for="star3"] {
          order: 3; }
        .reviewer__stars input + label[for="star2"] {
          order: 2; }
        .reviewer__stars input + label[for="star1"] {
          order: 1; }
        .reviewer__stars input + label:hover, .reviewer__stars input + label:hover ~ label {
          color: #ffdd74; }
  .reviewer textarea {
    border: 0;
    outline: 0;
    font-size: 2rem;
    padding: 2rem;
    height: 200px; }
  .reviewer__meta {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid grey; }
    .reviewer__meta > * {
      flex: 1; }



